.notesGroup{
  width: 350px;
  background-color: rgb(255, 255, 255);
  height: 100vh;
  padding: 2rem 1.2rem;
  padding-right: 0;
}

.notesSection{
  width: calc(100% - 350px);
  height: 100vh;
  background-color: #F7ECDC;
 
  position: relative;
  
}

.notesSection .blank_notes{
  width: 600px;
  padding: 2rem;
  max-width: 100%;
  text-align: center;
}

.notesBgImg{
  width: 500px;
  max-width: 100%;
}
.pocketNotesHead{
  font-size: 3rem;
}

.encryptedLine{
  position: absolute;
  bottom: 0px;
  left: 50%;
  transform: translateX(-50%);
}









/* notes Group */

.custom-right-padd{
  padding-right: 1.2rem;
}

.createNotesBtn{
  background-color: black;
  color: #fff;
  /* width: 100%; */
  padding: 0rem 2rem;
  border-radius: 40px;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  gap: 0.7rem;
}
.createNotesBtn span{
  font-size: 1.8rem;
  font-weight: 700;
  position: relative;
  bottom: 3px;
}

/* .createNotesBtn span{
  font-size: 1.5rem;
} */

.group{
  display: flex;
  align-items: center;
  gap: 1rem;
  cursor: pointer;
  /* border-radius: 30px 0px 0px 30px; */
}

.group *{
  pointer-events: none;
}

.selectedGroup{
background: #F7ECDC;
border-radius: 30px 0px 0px 30px;
}

.group .group-icon{
  width: 50px;
  height: 50px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  /* background: #0047FF; */
  border-radius: 50%;
  font-weight: 600;
  font-size: 1.2rem;

}

.group .group-name{
  font-weight: 700;
  font-size: 1.2rem;
}