.notes-heading {
    background: #E8E8E8;
}

.notes-heading .group-name {
    font-weight: 700;
    font-size: 1.3rem;
}

.EnterNotes{
    background: #E8E8E8;
    height: 200px;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 1rem 1.5rem;
}

.EnterNotes textarea{
    width: 100%;
    height: 100%;
    background: #FFFFFF;
    border: 1px solid #CCCCCC;
    border-radius: 9px;
    font-size: large;
    resize: none;

}

.EnterNotes img{
    position: absolute;
    width: 28px;
    bottom: 2rem;
    right: 2.5rem;
    cursor: pointer;
}

.notes-upper-section {
    height: calc(100vh - 200px);
}

#Notes{
    padding:2.5rem 3.5rem 2.5rem 2.5rem ;
    height: calc(100% - 200px);

}

#Notes .noteItem{
    display: flex;
    gap: 3rem;
}

#Notes .noteItem .noteTime{
    font-weight: 600;
}

#Notes .noteItem>div{
    flex-shrink: 0;
}